.nav-inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  list-style-type: none;

  @include _1500 {
    width: 80%;
  }

  @include _1280 {
    width: 70%;
  }

  @include _992 {
    width: 100%;
    flex-direction: column;
  }
}

.nav-inner a {
  position: relative;
  font-family: "Book Antiqua", "Times", serif;
  font-size: 29px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;

  @include _496 {
    display: block;
    width: 100%;
  }
}

.nav-inner img {
  margin-right: 16px;
}

.nav-inner li:last-of-type img {
  margin-right: -16px;
}

.nav-inner__link {
  @include _1500 {
    width: 350px;
    margin-bottom: 130px;
  }

  @include _1280 {
    width: 280px;
  }

  @include _1060 {
    width: 260px;
  }

  @include _992 {
    width: 350px;
    padding-left: 156px;
  }

  @include _496 {
    width: 100%;
    text-align: center;
  }
}

.nav-inner__link--play,
.nav-inner__link--face {
  padding-left: 90px;

  @include _496 {
    margin-bottom: 200px;
    padding-left: 0;
  }
}

.nav-inner__link--pencil {
  padding-left: 140px;

  @include _1500 {
    padding-left: 156px;
  }

  @include _496 {
    padding-left: 0;
  }
}

.nav-inner__link--air {
  padding-left: 156px;

  @include _496 {
    padding-left: 0;
  }
}

.nav-inner__link a::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.nav-inner__link--play a::before {
  width: 75px;
  height: 75px;
  left: -90px;
  background-image: url("../img/icon-play.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.nav-inner__link--pencil a::before {
  width: 211px;
  height: 179px;
  top: -80%;
  left: -145px;
  background-image: url("../img/pencils.png");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @include _1500 {
    left: -156px;
  }
}

.nav-inner__link--face a::before {
  width: 75px;
  height: 75px;
  left: -90px;
  background-image: url("../img/face.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.nav-inner__link--air a::before {
  width: 164px;
  height: 186px;
  top: -80%;
  left: -156px;
  background-image: url("../img/airplan.png");
}

.nav-inner__link a::before {
  @include _496 {
    position: absolute;
    top: -75%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.nav-inner__link--pencil a::before,
.nav-inner__link--air a::before {
  @include _496 {
    position: absolute;
    top: -120%;
  }
}
