.site-nav {
  width: 1045px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  list-style-type: none;

  @include _1135 {
    width: 760px;
  }

  @include _992 {
    width: 300px;
    flex-direction: column;
    margin: 0 auto;
    text-align: center;
  }

  @include _640 {
    width: 270px;
  }

  @include _496 {
    width: 80%;
  }

  @include _396 {
    width: 100%;
    margin-bottom: 35px;
  }
}

.site-nav li {
  padding-left: 15px;
  padding-right: 15px;

  @include _1135 {
    width: 210px;
    margin-bottom: 15px;
  }

  @include _992 {
    width: 100%;
  }
}

.site-nav a {
  font-family: inherit;
  font-size: 15px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;

  @include _1135 {
    width: 210px;
  }
}

.site-nav__item-active {
  background-color: #ffffff;
  font-weight: 700;
  line-height: 24px;
  padding: 4px 7px 6px;
}

.site-nav__item-active a {
  color: #a12628;
}
