.program {
  width: 420px;
  margin-bottom: 70px;
}

.program li {
  border-bottom: 1px solid #b3b3b3;
}

.program p {
  font-family: "Book Antiqua", "Times", serif;
  font-size: 21px;
  letter-spacing: -.5px;
  padding-top: 10px;
  padding-left: 15px;
  padding-right: 10px;
  padding-bottom: 10px;
  margin: 0;
}

.program__item-active {
  font-weight: 700;
  border: none;
}

.program__book li {
  border: none;
}

