.lesson {
  width: 712px;
}

.lesson__back {
  width: 706px;
  min-height: 370px;
  background-color: #e6e6e6;
  transform: rotate(3deg);
  margin-top: 40px;
}

.lesson__img {
  width: 706px;
  min-height: 370px;
  transform: rotate(-5deg);
}

.lesson__caption {
  display: block;
  font-family: "Book Antiqua", "Times", serif;
  font-size: 17px;
  line-height: 18px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  margin-top: 25px;
  margin-bottom: 20px;
}

.lesson__list {
  margin-bottom: 55px;
}

.lesson__list li {
  position: relative;
  padding-left: 30px;
}

.lesson__list li:before {
  position: absolute;
  content: "—";
  left: 0;
}

.lesson__item {
  display: flex;
}

.lesson__item > div {
  margin-right: 15px;
}

.lesson__list {
  margin-bottom: 55px;
}

.lesson__list ul {
  margin-top: 17px;
  margin-bottom: 17px;
}

.lesson__price {
  display: flex;
}

.lesson__price {
  width: 373px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  margin-top: 55px;
}

.lesson__price p {
  font-family: "Book Antiqua","Times",serif;
  font-size: 42px;
  font-weight: 300;
  letter-spacing: -0.5px;
  color: #e74c3c;
  margin: 0;
}

.lesson__btn {
  width: 170px;
  height: 40px;
}
